/* CSS RESet */
/* "*" is Universal Selector */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Dark Mode */

#dark {
  background-color: black;
  color: #f4eee0;
}

/* ======= mobile responsive ======== */
@media (max-width: 600px) {
  .footer {
    margin: 0;
    padding: 0;
    margin-top: 20px;
    margin-bottom: 20px;
  }
}
