.tackstack {
  padding-left: 130px;
  margin-top: 60px;
}

.tackstack hr {
  color: #138781;
  width: 400px;
  margin: 10px auto;
}

.tackstack h2 {
  font-weight: bold;
  color: #138781;
}

.tackstack .card {
  cursor: pointer;
}

.tackstack .tech-icon {
  font-size: 2rem;
  margin-right: 10px;
}

.tackstack .card:hover {
  background-color: #138781;
  color: white;
  /* border: none; */
  transition: 1s;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

/* ======= mobile responsive ======== */
@media (max-width: 600px) {
  .tachstack {
    padding: 0;
    margin: 0;
    margin-top: 50px;
  }

  .tackstack hr {
    width: 210px;
  }
}
